.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body{
  background-color:#333333;
  color:white;
}

.navigation{
  display:flex;
  flex-flow:row;
  justify-content: space-between;
  background-color:black;
  color:white;
  font-size:24px;
  padding:15px;
}

.nav-login-actions{
  display:flex;
  flex-flow:row;
}

.link-button{
  padding:5px;
  padding-left:10px;
  padding-right:10px;
  margin-right:10px;
  border: solid white 2px;
  border-radius:5px;
  user-select: none;
  cursor:pointer;
}

.link-button.share{
  background-color:#00948F;
}

.link-button.share:hover{
  filter:brightness(1.2);
}

.link-button.mod:hover{
  filter:brightness(1.2);
}

.link-button.mod{
  background-color:#00950F;
}

.stream{
  display:flex;
  flex-flow:row;
  padding:20px;
  justify-content: space-around;
  align-items:center;
  height:85vh;
  background-color:#333333;
}

.twitch-embed{
  width:70%;
  padding-left:10px;
  padding-right:10px;
  
}

.plugin-ui{
  width:30%;
  height:100%;
  background-color:#222222;
  border-radius: 10px;
}

.plugin-header{
  font-size:24px;
  text-align: center;
}

@media screen and (max-width:600px) {
  .stream{
    flex-flow:column;
  }
}